.youtube {


	position:relative;


	margin: auto;


	overflow:hidden;


}




html {
    position: relative;
    font-size: 100%;
    width: 100vw;
    height: 100%;
    overflow-x: hidden;

    @include r(1440) {
        font-size: 100%;
    }

    @include r(1200) {
        font-size: 90%;
    }

    @include r(1024) {
        font-size: 80%;
    }

    @include r(640) {
        font-size: 60%;
    }

}

body {
    font-family: $font;
    width: 100vw;
    height: 100%;
    position: relative;
    background-image: url('../img/background.jpg');
    background-repeat: none;
    background-position: center;
    background-size: cover;
     background-attachment: fixed;
}
